.post {
  width: 472px;
  height: 800px;
  border: 1px solid #dbdbdb;
  border-radius: 8px;
  background-color: #fff;
}

.home-post-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 10px;
}
.home-post-header-main {
  display: flex;
  align-items: center;
  gap: 10px;
}
.home-post-profile-icon {
  width: 32px;
  height: 32px;

  border-radius: 100%;
}

.home-post-img-cont {
  height: 587px;
  width: 472px;
  overflow: hidden;
}

.home-post-img {
  width: 100%;
  object-fit: cover;
  height: 587px;
}

.home-post-icons {
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.home-post-likes,
.home-post-mid-section,
.home-post-time,
.home-post-comment,
.home-post-icons {
  padding-left: 10px;
}
.home-post-icons-main {
  display: flex;
  gap: 20px;
  margin-bottom: 5px;
  align-items: center;
  justify-content: center;
  padding-top: 10px;
}

.home-post-caption-section {
  display: flex;
  gap: 5px;
}

.home-post-likes,
.home-post-caption-username {
  font-weight: 600;
}

.home-post-mid-section {
  display: flex;
  flex-direction: column;
  gap: 5px;
}

.home-post-time {
  padding-top: 5px;
  padding-bottom: 5px;
  color: #8e8e8e;
  font-size: 12px;
}

.home-post-comments-section {
  color: #8e8e8e;
  cursor: pointer;
}

.home-post-comment {
  display: flex;
  align-items: center;
  height: 40px;
  gap: 10px;

  border-top: 1px solid #dbdbdb;
}

.home-post-comment-form {
  display: flex;
}

.add-comment {
  outline: none;
  border: none;
  background-color: transparent;
  width: 374px;
}

.home-post-comment-post {
  color: #0095f6;
  outline: none;
  border: none;
  background-color: #fff;
}
.home-post-icons-main > div {
  cursor: pointer;
  display: flex;
  justify-content: center;
}

.home-post-icons-save {
  padding-right: 10px;
  cursor: pointer;
}
