.MiniProfile {
  display: flex;
  align-items: center;
  width: 347px;
  height: 48px;
}

.miniprofile-icon-cont {
  width: 32px;
  height: 32px;

  border-radius: 100%;
}

.miniprofile-icon {
  width: 100%;
  height: 32px;
  object-fit: cover;
  border-radius: 100%;
}

.miniprofile-main {
  display: flex;
  flex-direction: column;
  flex: 2;
  padding-left: 10px;
}

.miniprofile-main-followers {
  color: #8e8e8e;
  font-size: 12px;
}

.miniprofile-follow {
  color: #0095f6;
  font-size: 14px;
  cursor: pointer;
}
