.form-edit-profile {
  display: flex;
  flex-direction: column;
  height: 400px;
  align-items: center;
  justify-content: space-evenly;
  width: 360px;
  margin-top: 0px;
  padding-top: 0px;
}

.form-edit-profile-item {
  display: flex;
  gap: 10px;
  justify-content: center;
  align-items: center;
  width: 350px;
}

.form-edit-profile-img {
  height: 38px;
  width: 38px;
  border-radius: 100%;
}

.form-edit-profile-item-title {
  justify-self: flex-end;
  align-items: center;
  width: 80px;
  font-weight: 600;
}

.form-edit-img-container {
  display: flex;
  justify-content: center;
}

.form-edit-profile-button {
  background-color: #0095f6;
  color: #fff;
  padding: 7px 16px;
  font-weight: 600;
  border-radius: 8px;
  border: none;
}
