.Story {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.story-main-img-cont {
  height: 56px;
  width: 56px;
  border-radius: 100%;
  overflow: hidden;
}

.story-main-img {
  height: 56px;
  width: 100%;
  border-radius: 100%;
  object-fit: cover;
}

.story-username {
  font-size: 12px;
}
